import { useTranslation } from "react-i18next";
import style from "../AdmissionNotification.module.css";
import {
  assestsFeedUrl,
  assestsUtilityUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useNavigate, useParams } from "react-router-dom";
import { memo, useCallback } from "react";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
const YourAdmissionCardListCard = ({ process }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const params = useParams();

  const onNavigateApplication = useCallback(() => {
    navigate(`/q/${params.username}/member/your/admission?type=application`, {
      state: {
        appId: process?._id,
        applicationName: process?.applicationName,
        admissionAdmin: process?.admissionAdmin,
      },
    });
  }, [params.username, navigate]);

  return (
    <div className={style.ufu_application} onClick={onNavigateApplication}>
      <div className={style.ufu_your_application}>
        <img
          src={
            process?.admissionAdmin?.institute?.insProfilePhoto
              ? `${imageShowUrl}/${process?.admissionAdmin?.institute?.insProfilePhoto}`
              : `${assestsFeedUrl}/feed-user-avatar.svg`
          }
          alt="your icon"
          style={{
            borderRadius: "6px",
          }}
        />
        <div className={style.ufu_your_application_inner}>
          <h6>{process?.admissionAdmin?.institute?.insName ?? ""}</h6>
          <p>
            {process?.applicationName ?? ""} ({t("student")})
          </p>
        </div>
      </div>
      <div
        className={style.ufu_application_form}
        style={{
          backgroundColor: "rgba(18, 18, 18, 0.20)",
        }}
      >
        <h6>{t("click_here_to_view_process")}</h6>
        <img src={`${assestsUtilityUrl}/next.svg`} alt="more icon" />
      </div>
    </div>
  );
};

export default memo(YourAdmissionCardListCard);
